.App {
  font-family: sans-serif;
  text-align: center;
  background-color: rgb(190, 183, 183);
  height: 800px;
  width: 800px;
  margin: auto;
  padding-top: 20px;
}
.input {
  height: 65px;
  width: 350px;
  border-radius: 15px;
  border: 0px;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  font-size: 25px;
  text-align: center;
}
li {
  height: 65px;
  width: 550px;
  border-radius: 15px;
  border: 0px;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  font-size: 25px;
  text-align: center;
  line-height: 25px;
  margin: auto;
  margin-bottom: 20px;
  background-color: white;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
  font-weight: 600;
  color: rgb(128, 0, 90);
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
}
button {
  height: 80%;
  width: 100px;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  border-radius: 15px;
  border: 0px;
  margin-left: 15px;
  font-weight: 600;
  color: blueviolet;
}
.add {
  width: 55px;
  height: 65px;
  font-size: 25px;
  font-weight: 600;
}
